
html * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale; }
html {
    font-size: 62.5%; }
body {
    font-size: 1.5rem;
    font-family: $primary-font;
    background-color: gainsboro;
    position: relative;
    // padding: 22px
    padding: 1.375em; }

a {
    text-decoration: none;
    color: $blue; }
img {
    max-width: 100%; }

input, textarea, select {
    font-family: $primary-font;
    font-size: 1.6rem; }

p {
    font-family: $primary-font;
    font-weight: 400;
    color: $gray;
    line-height: 25px; }
h1, h2, h3, h4, h5, h6, p {
    margin: 0;
    padding: 0; }

h1 {
    color: gray; }
ul, ol {
    padding-left: 19px; }

.title {
    display: block; }
